* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', Arial, sans-serif;

}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

a {
  color: inherit;
  text-decoration: none;
}
li{
  list-style: none;
}
.hero {
  background-image: url("../assets/images/wm-1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  padding-bottom: 80px ;
}

.hero::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #B68C63;
  opacity: 0.4;
  z-index: 0;
}

.header {
  padding: 30px 0;
  position: relative;
  z-index: 1;
  margin-bottom: 50px;

}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #41210a;
  font-size: 13px;
  line-height: 1.55;
  font-weight: 600;
  text-transform: uppercase;
  border-width: 1px;
}

.header__logo {
  width: 250px;
  height: 150px;
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header__link {
  font-size: 13px;
  line-height: 1.55;
  font-weight: 600;
  text-transform: uppercase;
  border-width: 1px;
  border-radius: 30px;
  margin-left: 15px;
}
.header__link:hover {
color: #fec802;
}

.header__num {
  padding: 5px;
  margin-bottom: 5px;
  font-size: 20px;
  display: block;
  border-width: 2px;
  border-radius: 25px;
  opacity: 0.7;
  background-color: #fec802;
  border-color: #41210a;
  border-style: solid;
  box-shadow: 4px 4px 10px 2px rgba(65, 33, 10, 0.2);

}

.header__socials {
  display: flex;

}

.header__social {
  width: 55px;
  height: 55px;
  margin-left: 20px;
  cursor: pointer;
}

.header__social img {
  width: 100%;
  height: 100%;
}



/* hero */
.hero__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.content__title {
  font-size: 40px;
  line-height: 1.3;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 52px;
  position: relative;
  z-index: 1;
  color: #41210a;
  text-align: center;

}

.content__title::before {
  content: "";
  width: 101%;
  height: 50%;
  position: absolute;
  background-color: #F6C73B;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.9;
  bottom: -5px;
  z-index: -1;
}

.hero__subtitle {
  font-size: 35px;
  line-height: 1.3;
  font-weight: 500;
  text-transform: uppercase;
}

.hero__categories-top {
  display: flex;
  justify-content: space-between;
  max-width: 90%;
  margin: 0 auto;
  margin-bottom: 50px;
}

.hero__categories-bottom {
  display: flex;
  justify-content: space-between;
  max-width: 50%;
  margin: 0 auto;
}


.hero__category {
  border-width: 2px;
  border-radius: 25px;
  opacity: 0.7;
  background-color: #fec802;
  border-color: #41210a;
  border-style: solid;
  box-shadow: 4px 4px 10px 2px rgba(65, 33, 10, 0.2);
  width: 28%;
  height: 100px;
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 25px;
  line-height: 1.3;
  font-weight: 500;
  color: #41210a;
}
.hero__none{
  display: none;
}

.hero__category-bottom {
  width: 44%;
}

.section__contact {
  display: flex;
  justify-content: center;
  margin-top: 100px;

}

.custom-btn {
  text-transform: uppercase;
  font-size: 20px;
  width: 250px;
  height: 70px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
      7px 7px 20px 0px rgba(0, 0, 0, .1),
      4px 4px 5px 0px rgba(0, 0, 0, .1);
  outline: none;
}

.btn-5 {

  line-height: 42px;
  padding: 0;
  border: none;
  background: rgb(255, 27, 0);
  background: linear-gradient(0deg, rgba(255, 27, 0, 1) 0%, rgba(251, 75, 2, 1) 100%);
}

.btn-5:hover {
  color: #f0094a;
  background: transparent;
  box-shadow: none;
}

.btn-5:before,
.btn-5:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #f0094a;
  box-shadow:
      -1px -1px 5px 0px #fff,
      7px 7px 20px 0px #0003,
      4px 4px 5px 0px #0002;
  transition: 400ms ease all;
}

.btn-5:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}

.btn-5:hover:before,
.btn-5:hover:after {
  width: 100%;
  transition: 800ms ease all;
}





/* hero */



/* price */



.price {
  padding: 100px 0;
  background-color: #d8b484;
}

.price__title {
  max-width: 290px;
  margin: 0 auto;
  margin-bottom: 70px;
}

.price__head {
  border-bottom: 1px solid #90613f;
