/* faq */
.faq{
  padding: 80px 0;
}
.faq__wrapper {

  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 50px;
}
.faq__title{
  max-width: 700px;
  margin: 0 auto;
  margin-bottom: 100px;
}
.faq__desc{
  border-bottom: 1px solid #FEC802;
  padding: 20px 0;

}







.accordion {
  .accordion-item {
    border-bottom: 1px solid #F6C73B;
    button[aria-expanded='true'] {
      border-bottom: 1px solid #fec802;
    }
  }
  .faq__button {
    position: relative;
    display: block;
    text-align: left;
    width: 100%;
    padding: 1em 0;
    color: #000;
    font-size: 1.15rem;
    font-weight: 400;
    border: none;
    background: none;
    outline: none;
    &:hover, &:focus {
      cursor: pointer;
      color: #F6C73B;
      &::after {
        cursor: pointer;
        color: #FEC802;
        border: 1px solid #fec802;
      }
    }
    .accordion-title {
      padding: 1em 1.5em 1em 0;
    }
    .icon {
      display: inline-block;
      position: absolute;
      top: 18px;
      right: 0;
      width: 22px;
      height: 22px;
      border: 1px solid;
      border-radius: 22px;
      &::before {
        display: block;
        position: absolute;
        content: '';
        top: 9px;
        left: 5px;
        width: 10px;
        height: 2px;
        background: currentColor;
      }
      &::after {
        display: block;
        position: absolute;
        content: '';
        top: 5px;
        left: 9px;
        width: 2px;
        height: 10px;
        background: currentColor;
      }
    }
  }
  .faq__button[aria-expanded='true'] {
    color: #F6C73B;
    .icon {
      &::after {
        width: 0;
      }
    }
    + .accordion-content {
      opacity: 1;
      max-height: 9em;
      transition: all 200ms linear;
      will-change: opacity, max-height;
    }
  }
  .accordion-content {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: opacity 200ms linear, max-height 200ms linear;
    will-change: opacity, max-height;
    p {
      font-size: 1rem;
      font-weight: 300;
      margin: 2em 0;
    }
  }
}


